  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.l-footer {
  padding-block: clamp(60px, -16.8px + 12vw, 120px);
  background-color: rgb(246, 246, 246);
}

.l-footer__top {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 74px;
  padding-bottom: 74px;
  border-bottom: 1px solid #333333;
}

.l-footer__info p {
  margin-bottom: 0;
}

.l-footer__logo-wrap {
  width: 200px;
  height: 50px;
}

.l-footer__address-warp {
  margin-top: 25px;
}

.l-footer__tel-warp {
  margin-top: 25px;
}

.l-footer__nav-wrap {
  display: flex;
  gap: 70px;
}
@media (max-width: 768px) {
  .l-footer__nav-wrap {
    display: none;
  }
}

.l-footer__nav-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.l-footer__nav-list-item {
  border-bottom: 1px dashed rgb(204, 204, 204);
}

.l-footer__nav-list-item-link {
  display: block;
  position: relative;
  min-width: 150px;
  width: 100%;
  padding: 10px 0;
  font-weight: 500;
}
.l-footer__nav-list-item-link:after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  width: 15px;
  aspect-ratio: 1;
  padding-right: 10px;
  background-color: #333333;
  mask-image: url("data:image/svg+xml,%3Csvg width='15' height='16' viewBox='0 0 15 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3.125 8H11.875' stroke='%23333333' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M7.5 3.625L11.875 8L7.5 12.375' stroke='%23333333' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
  background-size: contain;
  mask-repeat: no-repeat;
  mask-position: center;
  vertical-align: middle;
  transition: transform 0.2s ease;
}
.l-footer__nav-list-item-link:hover:after {
  transform: translate(5px, -50%);
}

.l-footer__bottom {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .l-footer__bottom {
    display: block;
  }
}

.l-footer__copyright {
  font-size: 14px;
}

.l-footer__page-top {
  width: 32px;
  height: 32px;
  border: 1px solid #78cc98;
  transition: opacity 0.4s;
}
