  .l-footer__page-top {
    margin: 50px 0 0 auto;
  }
}
.l-footer__page-top a {
  display: block;
  width: 100%;
  height: 100%;
  background-color: #78cc98;
  mask-image: url("data:image/svg+xml;utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3E%3Cpath stroke='none' d='M0 0h24v24H0z' fill='none'/%3E%3Cpath d='M6 15l6 -6l6 6'/%3E%3C/svg%3E");
