/* header
---------------------------------------------------*/
.l-header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  background-color: rgb(255, 255, 255);
}

.l-header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  max-width: 1440px;
  height: var(--header-height);
  margin-inline: auto;
  padding: 0 15px;
}

.l-header__logo-wrap {
