  .l-header__cont {
    display: none;
  }
}

.l-header__contact-btn {
  position: relative;
  padding: 15px 20px;
  border: 1px solid #13ce83;
  border-radius: 5px;
  background-color: #13ce83;
  color: rgb(255, 255, 255);
}
.l-header__contact-btn:before {
  content: "";
  display: inline-block;
  width: 24px;
  aspect-ratio: 1;
  padding-right: 10px;
  background-color: rgb(255, 255, 255);
  mask-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4 4H20C21.1 4 22 4.9 22 6V18C22 19.1 21.1 20 20 20H4C2.9 20 2 19.1 2 18V6C2 4.9 2.9 4 4 4Z' stroke='white' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M22 6L12 13L2 6' stroke='white' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
  background-size: contain;
  mask-repeat: no-repeat;
  mask-position: center;
  vertical-align: middle;
  transition: all 0.6s;
}

.l-header__contact-btn:hover {
  background-color: rgb(255, 255, 255);
  color: #13ce83;
  opacity: 1;
}
.l-header__contact-btn:hover:before {
  background-color: #13ce83;
}

.l-inner {
  width: 100%;
  max-width: 1200px;
  padding-inline: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media (max-width: 1140px) {
  .l-inner {
    padding-inline: 20px;
  }
}
@media (max-width: 768px) {
  .l-inner {
    margin-right: auto;
    margin-left: auto;
  }
}

.l-lower-mv {
  position: relative;
  overflow: hidden;
  margin-top: var(--header-height);
}

.l-lower-mv__inner {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: clamp(60px, -74.4px + 21vw, 165px) clamp(20px, -31.2px + 8vw, 60px) clamp(60px, -61.6px + 19vw, 155px);
}

.l-lower-mv__head {
  transform: translateY(30px);
  opacity: 0;
  transition: all 0.6s ease;
}

.l-lower-mv__head.is-scroll {
  transform: translateY(0);
  opacity: 1;
}

.l-lower-mv__page-en {
  color: #78cc98;
  font-size: clamp(14px, 11.44px + 0.4vw, 16px);
  font-family: "Rubik", serif;
  font-weight: 500;
  text-transform: uppercase;
}

.l-lower-mv__page-ja {
  font-size: clamp(30px, 6.96px + 3.6vw, 48px);
  font-weight: 500;
  letter-spacing: 0.08em;
  line-height: 1.2;
}

.l-lower-mv__breadcrumb {
  transform: translateX(30px);
  opacity: 0;
  transition: all 0.6s ease 0.6s;
}

.l-lower-mv__breadcrumb.is-scroll {
  transform: translateX(0);
  opacity: 1;
}

.l-lower-mv__image {
  width: 100%;
  height: min(35vw, 355px);
  object-fit: cover;
}

.l-lower-mv__picture .l-lower-mv__image {
  opacity: 0;
  transition: opacity 0.6s ease 0.3s, transform 0.6s ease 0.3s;
}

.l-lower-mv__picture.is-scroll .l-lower-mv__image {
  opacity: 1;
}

.l-nav-btn {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1100;
  width: 50px;
  height: 50px;
  background: none;
  cursor: pointer;
}
