.wpcf7-form input[type=radio] {
  position: absolute;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  overflow: hidden;
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  opacity: 0;
}

input[type=radio] + .wpcf7-list-item-label {
  position: relative;
  padding-left: 30px;
  cursor: pointer;
}
input[type=radio] + .wpcf7-list-item-label:before, input[type=radio] + .wpcf7-list-item-label:after {
  display: block;
  border-radius: 50%;
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
}
input[type=radio] + .wpcf7-list-item-label:before {
  background-color: rgb(255, 255, 255);
  border: 1px solid rgb(204, 204, 204);
  border-radius: 50%;
  width: 20px;
  height: 20px;
  left: 5px;
}
input[type=radio] + .wpcf7-list-item-label:after {
  background-color: #78cc98;
  border-radius: 50%;
  opacity: 0;
  width: 14px;
  height: 14px;
  left: 8px;
}

.wpcf7-form input[type=radio]:checked + .wpcf7-list-item-label::after {
  opacity: 1;
}

.wpcf7-form input[type=checkbox] {
  position: absolute;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  overflow: hidden;
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  opacity: 0;
}

input[type=checkbox] + .wpcf7-list-item-label {
  position: relative;
  padding-left: 30px;
  cursor: pointer;
}
input[type=checkbox] + .wpcf7-list-item-label:before, input[type=checkbox] + .wpcf7-list-item-label:after {
  display: block;
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
}
input[type=checkbox] + .wpcf7-list-item-label:before {
  background-color: rgb(255, 255, 255);
  border: 1px solid rgb(204, 204, 204);
  width: 20px;
  height: 20px;
  left: 5px;
}
input[type=checkbox] + .wpcf7-list-item-label:after {
  width: 18px;
  height: 11px;
  border-top: 3px solid #13ce83;
  border-right: 3px solid #13ce83;
  transform: translateY(-50%) rotate(135deg);
  left: 7px;
  top: 9px;
  opacity: 0;
}

.wpcf7-form input[type=checkbox]:checked + .wpcf7-list-item-label::after {
  opacity: 1;
}

